.miniscroller {
  width: 100vw;
  overflow: hidden;
  height: 475px;
  text-align: center;
}

.cards-slider-wrapper {
  display: flex;
  transition: transform 300ms ease-in;
  width: 80vw;
  margin: 0 5vw;
  height: 100%;
}

.card {
  flex: 1;
  opacity: 0.5;
  transform: scale(0.8);
  transition: opacity 300ms linear;
  transition: transform 300ms ease-in;
}

.snippet-container {
  background: #363638;
  display: flex;
  padding-left: -5vw;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  text-align: center;
  height: 400px;
  max-width: 280px;
  padding: 1vh 1vw;
  box-shadow: 5px 7px 5px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
}

.snippet-image {
  width: 100%;
  border: 1px solid #363638;
}

.snippet-header {
  margin: 1vh 1vw;
  color: white;
}

.snippet-description {
  color: white;
  font-size: 16px;
}

.hover {
  background: transparent;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 280px;
  height: 100%;
  top: 0;
}

.hover:hover {
  background: white;
  opacity: 0.85;
}

.hover:hover .snippet-button {
  display: block;
}

.snippet-button {
  color: #363638;
  display: none;
  font-size: 22px;
  text-decoration: none;
  padding: 16px 24px;
  margin: 3vh 0;
  border: 1px solid #363638;
}

.snippet-button:hover {
  color: white;
  background: #363638;
}

.cards-slider.active-slide-0 #card-0 {
  opacity: 1;
  transform: scale(1);
}

.cards-slider.active-slide-1 #card-1 {
  opacity: 1;
  transform: scale(1);
}

.cards-slider.active-slide-2 #card-2 {
  opacity: 1;
  transform: scale(1);
}

.cards-slider.active-slide-3 #card-3 {
  opacity: 1;
  transform: scale(1);
}

.cards-slider.active-slide-4 #card-4 {
  opacity: 1;
  transform: scale(1);
}

.cards-slider.active-slide-5 #card-5 {
  opacity: 1;
  transform: scale(1);
}

.cards-slider.active-slide-6 #card-6 {
  opacity: 1;
  transform: scale(1);
}

.cards-slider.active-slide-7 #card-7 {
  opacity: 1;
  transform: scale(1);
}

.cards-slider.active-slide-8 #card-8 {
  opacity: 1;
  transform: scale(1);
